import 'core-js/es6/promise'
import 'core-js/es6/string'
import 'core-js/es7/array'
// import cssVars from 'css-vars-ponyfill'
import Vue from 'vue'
import BootstrapVue from 'bootstrap-vue'
import App from './App'
import router from './router'

// todo
// cssVars()

Vue.use(BootstrapVue)

Vue.prototype.$ajax = (c, params, cb) => {
    var host = 'http://xtzero.me/dressupAsMeituan/api/';
    var apiAddr = host + c + '.php?';
    var paramArr = [];
    for (var i in params) {
        paramArr.push(i + '=' + params[i]);
    }

    var paramStr = paramArr.join('&');
    var api = apiAddr + paramStr;

    var xhr = new XMLHttpRequest();
    console.log('ajax:url(' + api + ')');

    xhr.open('GET', api, true);

    xhr.onload = function (res) {
        var ajaxRes = JSON.parse(res.currentTarget.responseText);
        if (ajaxRes.code == 10001) {
            window.location.hash = '/login';
        } else {
            cb && cb(ajaxRes);
        }

    }

    xhr.onerror = function (res) {
        cb && cb({
            code: -10000,
            msg: 'ajax请求错误',
            data: res
        });
    }

    xhr.send();
}

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    template: '<App/>',
    components: {
        App
    }
})
